<template>
  <span v-if="logged">
    <div class="topic-title">Profile</div>
    <div class="topic-name">
      <router-link :to="'/user/' + getAddress().substr(12)">Posts</router-link><br />
      <!--
      <a href="javascript:void(0);" @click="">Following</a><br />
      <a href="javascript:void(0);" @click="">Followers</a><br />
      <a href="javascript:void(0);" @click="">Muted</a>
      //-->
    </div>
  </span>
</template>

<script>

  export default {
      name: 'UserNav',
      props: {
      },
      data () {
        return {
        }
      },
      components: {
      },
      computed: {
        logged: function () {
          if (this.loggedViewMode() || this.loggedSpendMode()) {
            return true
          }
          return false
        }
      },
      methods: {
      }
  };
</script>

<style>
</style>



