export default {
  data: function () {
    return {
      oldUrl: null,
      oldTitle: null
    }
  },
  mounted () {
    if (sessionStorage.getItem('oldUrl') !== null && sessionStorage.getItem('oldUrl') !== undefined) {
      this.oldUrl = sessionStorage.getItem('oldUrl')
    }
    if (sessionStorage.getItem('oldTitle') !== null && sessionStorage.getItem('oldTitle') !== undefined) {
      this.oldTitle = sessionStorage.getItem('oldTitle')
    }
  },
  created () {
  },
  methods: {
    openModal (modalId, onTop = false) {
      if (sessionStorage.getItem('oldUrl') === null || sessionStorage.getItem('oldUrl') === undefined) {
        sessionStorage.setItem('oldUrl', window.location.href)
      }
      if (sessionStorage.getItem('oldTitle') === null || sessionStorage.getItem('oldTitle') === undefined) {
        sessionStorage.setItem('oldTitle', document.title)
      }
      this.oldUrl = sessionStorage.getItem('oldUrl')
      this.oldTitle = sessionStorage.getItem('oldTitle')
      const modal = document.getElementById(modalId)
      modal.style.display = "block"
      if (onTop) {
        modal.style.zIndex = 3
      }

      // When the user clicks anywhere outside of the modal, close it
      let that = this
      window.onclick = function(event) {
        if (event.target == modal) {
          document.title = that.oldTitle
          window.history.pushState('', '', that.oldUrl)
          sessionStorage.removeItem('oldUrl')
          sessionStorage.removeItem('oldTitle')
          modal.style.display = "none";
        }
      }
    },
    closeModal (modalId) {
      document.title = this.oldTitle
      window.history.pushState('', '', this.oldUrl)
      sessionStorage.removeItem('oldUrl')
      sessionStorage.removeItem('oldTitle')
      const modal = document.getElementById(modalId)
      modal.style.display = "none"
    }
  }
}



